import { useState } from 'react';
import { useSelector } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import {
    useTranslate,
    DashboardMenuItem,
    MenuItemLink,
    MenuProps,
} from 'react-admin';
import BalanceIcon from '@material-ui/icons/AttachMoney';
import OrdersIcon from '@material-ui/icons/ShoppingCart';
import SubMenu from './SubMenu';
import { AppState } from '../types';

type MenuName = 'menuCatalog' | 'menuSales' | 'menuCustomers';

const Menu = ({ dense = false }: MenuProps) => {
    const translate = useTranslate();
    useSelector((state: AppState) => state.theme);
    const classes = useStyles();

    const [state, setState] = useState({
        menuCatalog: true,
        menuSales: true,
        menuCustomers: true,
    });
    const handleToggle = (menu: MenuName) => {
        setState((state) => ({ ...state, [menu]: !state[menu] }));
    };

    return (
        <div className={classes.root}>
            <DashboardMenuItem />
            <SubMenu
                handleToggle={() => handleToggle('menuCatalog')}
                isOpen={state.menuCatalog}
                name={translate('Balance')}
                icon={<BalanceIcon />}
                dense={dense}
            >
                <MenuItemLink
                    to="/balance"
                    primaryText={translate('Balance')}
                    dense={dense}
                />
                <MenuItemLink
                    to="/request"
                    primaryText={translate('Request')}
                    dense={dense}
                />
            </SubMenu>
            <MenuItemLink
                to="/orders"
                primaryText={translate('Orders')}
                leftIcon={<OrdersIcon />}
                dense={dense}
            />
        </div>
    );
};

const useStyles = makeStyles((theme) => ({
    root: {
        marginTop: theme.spacing(1),
    },
}));

export default Menu;
