import {
    List,
    ListProps,
    Datagrid,
    TextField,
    NumberField,
    Filter,
    FilterProps,
    DateInput,
} from 'react-admin';
import { makeStyles } from '@material-ui/core/styles';
import BalanceShow from './BalanceShow';

const ListFilters = (props: Omit<FilterProps, 'children'>) => (
    <Filter {...props}>
        <DateInput source="date_gte" alwaysOn />
        <DateInput source="date_lte" alwaysOn />
    </Filter>
);

const useStyles = makeStyles((theme) => ({
    hiddenOnSmallScreens: {
        display: 'table-cell',
        [theme.breakpoints.down('md')]: {
            display: 'none',
        },
    },
}));

const BalanceList = (props: ListProps) => {
    const classes = useStyles();
    return (
        <List
            {...props}
            filters={<ListFilters />}
            perPage={25}
            sort={{ field: 'created_at', order: 'desc' }}
            bulkActionButtons={false}
        >
            <Datagrid rowClick="expand" expand={<BalanceShow />}>
                <TextField source="id" label="ID" />
                <TextField
                    source="activity"
                    label="Activities"
                    sortable={false}
                />
                <NumberField
                    source="change"
                    locales={['en']}
                    options={{
                        style: 'currency',
                        currency: 'USD',
                    }}
                    label="Amount"
                />
                <NumberField
                    source="current"
                    locales={['en']}
                    options={{
                        style: 'currency',
                        currency: 'USD',
                    }}
                    label="Balance"
                />
                <TextField
                    source="order_reference"
                    label="Order"
                    headerClassName={classes.hiddenOnSmallScreens}
                    cellClassName={classes.hiddenOnSmallScreens}
                />
                <TextField
                    source="order_line"
                    label="Order Line"
                    sortable={false}
                />
                <TextField source="created_at" />
                <TextField
                    source="note"
                    sortable={false}
                    headerClassName={classes.hiddenOnSmallScreens}
                    cellClassName={classes.hiddenOnSmallScreens}
                />
            </Datagrid>
        </List>
    );
};

export default BalanceList;
