import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import { useShowController, ReferenceField } from 'react-admin';
import Basket from '../orders/OrderList';
import { Balance } from '../types';

const BalanceShow = (props: any) => {
    const { record } = useShowController<Balance>(props);
    const classes = useStyles();

    if (!record) return null;

    return (
        <Card className={classes.root}>
            <CardContent>
                <Grid container spacing={2}>
                    <Grid item xs={6}>
                        <Typography variant="h6" gutterBottom>
                            {`Order Line Number: ${record.order_reference}`}
                        </Typography>
                        <Typography variant="subtitle1" gutterBottom>
                            {`Activities: ${record.activity} / Amount: $${record.change}`}
                        </Typography>
                    </Grid>
                    <Grid item xs={6}>
                        <Typography
                            variant="subtitle1"
                            gutterBottom
                            align="right"
                        >
                            View Order Detail
                        </Typography>
                    </Grid>
                </Grid>
                <div className={classes.balance}>
                    <ReferenceField
                        resource="balance"
                        reference="orders"
                        source="id"
                        basePath="/balance"
                        record={record}
                        link={false}
                    >
                        <Basket />
                    </ReferenceField>
                </div>
            </CardContent>
        </Card>
    );
};

export default BalanceShow;

const useStyles = makeStyles({
    root: { width: 600, margin: 'auto' },
    spacer: { height: 20 },
    balance: { margin: '10px 0' },
});
